<!-- eslint-disable vue/no-mutating-props -->
<template>
  <div>
    <Modal v-model="dialog.show" title="预览主机" width="950" :mask-closable="false" :footer-hide="true">
      <alert>一共查询到:{{ totalCount }}条数据</alert>
      <Input v-model="searchVal" style="width: 330px; margin: 0px" clearable placeholder="输入实例ID、主机名、内网IP搜索" @keydown.enter.native="handleSearch" />

      <Button style="margin: 3px" @click="handleSearch">搜索</Button>

      <div style="margin-top: 5px">
        <Table stripe border size="small" :columns="columns" :data=tableData ></Table>
      </div>
      
      <div style="margin: 10px; overflow: hidden">
        <Page :total="totalCount" :current="pageNum" :page-size="pageSize" show-total @on-change="handlerChangePage" />
      </div>
    </Modal>
  </div>
</template>
<script>
export default {
  name: 'preViewHost',
  props: {
    dialog: {
      type: Object,
      default: {}
    },
  },
  data() {
    return {
      serverList: [], //初始总数
      tableData: [],
      searchVal: null,
      pageTotal: 0,
      pageSize: 10,
      pageNum: 1,
      columns: [
        {
          title: '实例ID',
          key: 'instance_id',
          align: 'center',
          sortable: true,
        },
        {
          title: '主机名称',
          key: 'name',
          align: 'center',
          minWidth: 100,
          sortable: true,
        },
        {
          title: '内网IP',
          key: 'inner_ip',
          align: 'center',
          width: 140,

          sortable: true,
        },
        {
          title: '外网IP',
          key: 'outer_ip',
          align: 'center',
          width: 140,
          sortable: true,
        },
        {
          title: '运行状态',
          key: 'state',
          align: 'center',
          width: 135,
          sortable: true,
          render: (h, params) => {
            const { row } = params
            // eslint-disable-next-line no-nested-ternary
            const color = row.state === '运行中' ? 'success' : row.state === '关机' ? 'error' : row.state === '创建中' ? 'primary' : 'error'
            // eslint-disable-next-line no-nested-ternary
            const text = row.state === '运行中' ? '运行中' : row.state === '关机' ? '已关机' : row.state === '创建中' ? '创建中' : 'Fail'
            return h(
              'Tag',
              {
                props: {
                  type: 'dot',
                  color,
                },
              },
              text,
            )
          },
        },
        {
          title: 'Agent状态',
          key: 'agent_status',
          align: 'center',
          width: 135,
          sortable: true,
          render: (h, params) => {
            let status = params.row.agent_status
            if (status == '1') {
              return h('div', [h('Tag', { props: { color: 'green', type: 'border' } }, '在线')])
            } else if (status == '2') {
              return h('div', [h('Tag', { props: { color: 'red' } }, '离线')])
            } else {
              return h('div', [h('Tag', { props: { color: 'error' } }, '未知')])
            }
          }
        },
      ],
    }
  },
  watch: {
  },
  computed: {
    totalCount() {
      return this.serverList.length
    }
  },
  methods: {
    handlerChangePage (value) {
      this.pageNum = value
      this.setCurrentPageData()
    },
    // 分页处理
    setCurrentPageData () {
      let begin = (this.pageNum - 1) * this.pageSize;
      let end = this.pageNum * this.pageSize;
      this.tableData = this.serverList.slice(begin, end);
    },
    checkExecList (item) {
      return item.instance_id.indexOf(this.searchVal) >= 0 || item.name.indexOf(this.searchVal) >= 0 || item.inner_ip.indexOf(this.searchVal) >= 0
    },
     // 前端搜索
    handleSearch () {
      if (this.searchVal) {
        this.pageNum = 1
        this.tableData = this.serverList.filter(this.checkExecList)
      } else {
         this.pageNum = 1
         this.setCurrentPageData ()
      }
    }
  },
}
</script>
